.bid-btn {
    overflow: unset;
    background-image: none;
    background: #8ec63f;
    border: 2px solid #8ec63f;
    color: #232a2d;
    cursor: pointer;
    display: inline-block;
    font-family: 'Roboto Condensed';
    font-weight: 700;
    padding: 5px 23px;
    font-size: 1rem;
    box-sizing: border-box;
    vertical-align: middle;
    text-transform: uppercase;
    letter-spacing: .5px;
    position: relative;
    z-index: 0;
}

.jumbotron {
    background-color: white;
    color: #F47920;
    font-family: "Roboto Condensed";
}

p {
    color: #232A2D;
}
.card {
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  overflow: hidden;
  font-family: "Roboto Condensed";
}

.click-item:hover{
  box-shadow: 5px 5px 15px; 
  -webkit-transform: scale(1.1); 
          transform: scale(1.1);
  cursor: pointer;
}

img {
  max-height: 250px;
}

